:root {
    --background-color: #ffffff;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}
html {
    font-family: sans-serif;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
body {
    font-family: sans-serif;
    font-size: 14px;
    line-height: 20px;
    color: #191b1d;
    padding: 0;
    margin: 0;

}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}
div {
    display: block;
}
h4 {
    display: block;
    margin-top: 20px;
    margin-bottom: 20px;
    color: #191b1d;
    font-size: 28px;
    line-height: 34px;
    font-weight: 600;
}
h6 {
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: Inter,sans-serif;
    color: #191b1d;
    font-size: 1.3em;
    line-height: 1.3em;
    font-weight: 600;
}
.body-wrapper {
    width: 100%;
    overflow: hidden;
    background-color: #faf8f5;
}
.container {
    position: relative;
    max-width: 1280px;
    padding : 0 clamp(24px, 0.8vw ,40px);
    margin: 0 auto;
}
.section {
    position: relative;
    padding: 80px 0;
    border-right: 1px dashed rgba(196,205,213,.4);
    border-left: 1px dashed rgba(196,205,213,.4);
    .center-align {
        display: -webkit-box;
        display: -webkit-flex;
        display: -ms-flexbox;
        display: flex;
        padding-top: 100px;
        padding-bottom: 100px;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
        -ms-flex-direction: column;
        flex-direction: column;
        -webkit-box-pack: center;
        -webkit-justify-content: center;
        -ms-flex-pack: center;
        justify-content: center;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
    }
}
.row {
    z-index: 0;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    min-height: 100%;
}
.layout-grid {
    display: grid;
    grid-auto-columns: 1fr;
    width: 100%;
    justify-content: space-between;
}
.hero-columns {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    column-gap: 80px;
    h1 {
        font-size: 48px;
        line-height: 56px;
    }
    p {
        color: #474545;
        font-size: 18px;
        line-height: 26px;
    }
    a {
        display: inline-block;
        position: relative;
        font-size: 18px;
        line-height: 32px;
        font-weight: 500;
        width: 100%;
    }
}
.hero-face-container {
    display: none;
}
.nav-card {
    display: flex;
    flex-direction: column;
}
.logo-image {
    max-width: 16rem;
    margin: 0 auto;
}
.large-text {
    color: #474545;
    font-size: 18px;
    line-height: 26px;
}
.card-body {
    display: flex;
    padding: 0.8rem 0;
    width: 100%;
    flex-direction: column;
}
.bootcamp-card-image {
    width: 100%;
    height: auto;
    margin: 0 auto;
}
.fill {
    padding: 12px 20px;
    border: 3px solid #191b1d;
    border-radius: 8px;
    background-color: #191b1d;
    box-shadow: none;
    -webkit-transition: border-color .2s,background-color .2s,box-shadow .2s;
    transition: border-color .2s,background-color .2s,box-shadow .2s;
    font-family: Inter,sans-serif;
    color: #fff;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    text-align: center;
    margin: 0 auto;
}
.full-line {
    width: 25%;
    height: 2px;
    margin-top: 16px;
    margin-bottom: 16px;
    background-color: #dfdbd1;
}
.small-break {
    height: 24px;
}
.tiny-break {
    height: 8px;
}
.small-text {
    font-family: Inter,sans-serif;
    color: #706e6c;
    font-size: 1em;
    line-height: 1.3em;
    font-weight: 400;
}
.black {
    color: #1e1e1e;
}
.inline {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    .starts {
        margin-right: 800px;
    }
}
.title {
    margin-bottom: 0.8rem;
    color: rgba(30,30,30,.5);
    font-size: 13px;
    line-height: 24px;
}
.capitals {
    margin-bottom: 8px;
    color: #6d6b6b;
    font-weight: 500;
    letter-spacing: .5px;
    text-transform: uppercase;
}
.no-margin {
    margin-bottom: 0.8rem;
    color: #191b1d;
    font-size: 20px;
    line-height: 24px;
    font-weight: 600;
}
.block {
    display: flex;
    flex-direction: column;
    .no-margin,.capitals,.large-text {
        padding-left: 0.8rem;
    }
    .no-margin {
        border-left: 0.1rem solid red;
    }
}
.avatar {
    width: 2.4rem;
    height: 2.4rem;
    margin-right: 0.8rem;
    border-color: #030132;
    border: 4px solid black;
    border-radius: 100px;
}
.avatar-container {
    display: flex;
    margin-right: auto;
}
.promo-image {
    border-radius: 12px;
    background-image: -webkit-gradient(linear,left bottom,left top,from(rgba(26,34,43,.5)),to(rgba(0,0,0,.2))),url("https://global-uploads.webflow.com/5f8e97a7f04a9fe98d9401a6/5f8e97a7f04a9f808b94032f_career_hero%20(1).jpg");
    background-image: linear-gradient(0deg,rgba(26,34,43,.5),rgba(0,0,0,.2)),url("https://global-uploads.webflow.com/5f8e97a7f04a9fe98d9401a6/5f8e97a7f04a9f808b94032f_career_hero%20(1).jpg");
    background-position: center;
    background-size: auto,cover;
    height: 30rem;
}
.promo-section {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -webkit-box-pack: center;
    -webkit-justify-content: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align:center;
    align-items: center;
    color: white;
    padding: 1.6rem;
}
.slide-menu {
    padding :1.6rem 1.2rem ;
    background-color: #faf8f5; 
}
.slider-header {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #dfdbd1;
    padding-bottom: 1.2rem;
    margin-bottom: 1.2rem;
}
.slider-body {
    display: none;
}
.outline {
    display: inline-block;
    padding: 12px 20px;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -ms-flex-align: center;
    align-items: center;
    border: 3px solid #191b1d;
    border-radius: 8px;
    background-color: transparent;
    -webkit-transition: color .2s,box-shadow .2s,background-color .2s;
    transition: color .2s,box-shadow .2s,background-color .2s;
    font-family: Inter,sans-serif;
    color: #191b1d;
    font-size: 16px;
    line-height: 24px;
    font-weight: 500;
    &:focus {
        color: red;
    }
}
.navbar-logo {
    display: flex;
    .logo-text {
        display: none;
    }
}
.navbar-logo-image {
    max-height: 6vh;
}
.footer-header {
    border-bottom: 1px solid #dfdbd1;
    padding-bottom: 0.4rem;
    margin-bottom: 0.4rem;
}
.footer-column-item {
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
}
@media screen and (min-width: 40em) {
    .hero-columns {
        -ms-grid-columns: 1fr 0.8fr;
        grid-template-columns: 1fr 0.8fr;
        -ms-grid-rows: auto;
        grid-template-rows: auto;
    }
    .three-columns {
        -ms-grid-template-columns: 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr;
    }
    .row {
        padding: 2.4rem;
    }
    .hero-face-container {
        display: block;
    }
    .logo-image {
        max-width: 8rem;
    }
    .navbar-logo {
        display: flex;
        align-items: center;
        .logo-text {
            display: block;
            padding: 0.8rem;

        }
    }
}
@media screen and (min-width: 992px) {
    .hero-button {
        max-width: 30%;
    }
    .company-logo {
        width: 100%;
        margin-bottom: 120px;
        -webkit-box-align: center;
        -webkit-align-items: center;
        -ms-flex-align: center;
        align-items: center;
        -ms-grid-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        -ms-grid-rows: auto;
        grid-template-rows: auto;
    }
    .slider-header {
        border: none;
    }
    .slide-menu {
        display: flex;
        justify-content: space-between;
    }
    .slider-body {
        display: block;
        .footer-column {
            display: flex;
            .footer-header {
                display: none;
            }
            .footer-column-item {
                padding: 0.8rem;
            }
        }
    }
    .outline {
        display: none;
    }
}